import React, { Component } from "react";
import { updateRenderState } from ".";
import MealViewer from "./MealViewer";

export default class MealCard extends Component {
	mountMealViewer = () => {
		const [path, hash] = [window.location.pathname, window.location.hash];

		if (path === "/favorites" && hash === "#Meals") {
			window.history.pushState("", "", "/favorites#Meals#MealViewer");
		} else if (path === "/favorites" && hash.length !== 0) {
			window.history.pushState("", "", "/favorites#Meals#" + hash.split("#")[2] + "#MealViewer");
		};
		
		updateRenderState(<MealViewer key={Math.random() * 1000} doAnimation={true} name={this.props.name} kcal={this.props.kcal} />);
	};

	render() {
		return (
			<div className='mealCardContainer' onClick={this.mountMealViewer}>
				<div className='mealCardIcon'></div>
				<div className='mealCardInfoContainer'>
					<p className='mealCardName'>{this.props.name}</p>
					<p className='mealCardIngredients'>
						{this.props.ingredients === 1 ? "مكون واحد" : this.props.ingredients === 2 ? "مكونان" : this.props.ingredients}
						{this.props.ingredients === 1 || this.props.ingredients === 2 ? "" : " مكونات"}
					</p>
					<p className='mealCardKcal'>{~~this.props.kcal} سعرة حرارية</p>
				</div>
			</div>
		);
	}
}
